.dtn-timer {
    --dtn-timer__color: var(--dtn-clr-background);
    --dtn-timer__background: var(--dtn-clr-base);
    --dtn-timer__border-color: var(--dtn-clr-base);
    font-weight: 800;
    font-size: 18px;
    color: var(--dtn-timer__color);
    background: var(--dtn-timer__background);
    border: 2px solid var(--dtn-timer__border-color);
    border-radius: 4px;
    padding: 1px 4px;
    line-height: 126%;
    margin-left: 8px;

    &:first-child {
        margin-left: 0;
    }

    &--beeping {
        animation: dtn-timer-beep 2s infinite;
    }
}

@keyframes dtn-timer-beep {
    0% {
        --dtn-timer__background: var(--dtn-clr-base);
        --dtn-timer__color: var(--dtn-clr-background);
    }

    50% {
        --dtn-timer__background: transparent;
        --dtn-timer__color: var(--dtn-timer__border-color);
    }

    100% {
        --dtn-timer__background: var(--dtn-clr-base);
        --dtn-timer__color: var(--dtn-clr-background);
    }
}
