.dtn-notifier {
    --dtn-notifier__color: #000;
    --dtn-notifier__background: var(--dtn-clr-ctx-warning);
    position: absolute;
    top: 0;
    left: calc(50% - 225px);
    z-index: 1;
    width: 550px;
    background: var(--dtn-notifier__background);
    padding: 16px 20px;
    border-radius: 0 0 12px 12px;
    color: var(--dtn-notifier__color);
    user-select: text;

    &__notification {
        &__title,
        &__content {
            font-weight: 600;
        }

        &__title {
            font-size: 18px;
            padding-bottom: 4px;
            line-height: 126%;
        }

        &__content {
            font-size: 14px;
            line-height: 19px;
        }
    }
}
