.dtn-header {
    display: flex;
    height: 71px;
    grid-area: header;
    justify-content: space-between;
    align-items: center;

    &__logo {
        display: block;
        width: 198px;
        height: 27px;
        background: url('/assets/img/logo.svg') 0 0 no-repeat;
    }

    &__time-value {
        padding-left: 5px;
        padding-right: 5px;
        font-weight: 600;
        font-size: 18px;
        line-height: 126%;
        will-change: contents;
    }

    &__time-container {
        margin-right: 52px;
    }
}
