@mixin dtn-var-colors {
    --dtn-clr-base: #fff;
    --dtn-clr-background: #1c1c1a;
    --dtn-clr-primary: var(--dtn-clr-base);
    --dtn-clr-secondary: #b8babb;
    --dtn-clr-secondary-lt: #ccc;
    --dtn-clr-container-background: #353735;
    --dtn-clr-container-separator: #767776;
    --dtn-clr-ctx-warning: #efe95c;
    --dtn-clr-ctx-error: #ca5122;
    --dtn-clr-ctx-success: #27ae60;

    /*
    TODO: uncomment when dark theme is ready
    color-scheme: light dark;
    .dark-scheme() {
        --dtn-clr-base-ltst: #3d3d3d;
        --dtn-clr-contrast: #3d3d3d;
    }
    [data-color-scheme="dark"] {
        .dark-scheme();
    }

    */
}
