.dtn-panel {
    --dtn-panel__border-radius: var(--dtn-border-radius);
    --dtn-panel__item__border-color: var(--dtn-clr-background);
    --dtn-panel__background: var(--dtn-clr-container-background);
    --dtn-panel__item-title__color: var(--dtn-clr-secondary);
    --dtn-panel__background--recommendation: var(--dtn-clr-ctx-error);
    --dtn-panel__background--success: var(--dtn-clr-ctx-success);
    display: flex;
    padding: 12px 16px;
    background: var(--dtn-panel__background);
    border-radius: var(--dtn-panel__border-radius);

    &--absolute {
        position: absolute;
        top: 0;
        left: calc(100% - 8px);
        bottom: 0;
        min-width: 120px;
        margin: 0;
    }

    &--general {
        min-height: 60px;
        margin-bottom: 50px;
        padding: 0 8px;
    }

    &--fitContent {
        width: fit-content;
    }

    &__item {
        padding: 10px 8px;
        border-left: 1px solid var(--dtn-panel__item__border-color);
        min-width: 100px;

        &:first-child {
            border-left: none;
        }
    }

    &__item-title {
        font-weight: bold;
        font-size: 10px;
        line-height: 14px;
        text-transform: uppercase;
        color: var(--dtn-panel__item-title__color);
        margin-bottom: 4px;
    }

    &__item-content {
        font-weight: 600;
        font-size: 18px;
        line-height: 126%;
    }

    &--recommendation {
        --dtn-panel__background: var(--dtn-panel__background--recommendation);
        font-weight: 600;
        font-size: 18px;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        padding: 0 12px;
        min-height: 60px;
        max-width: 420px;

        &--icon {
            .dtn-icon {
                margin-right: 6px;
            }
        }

        &--spaced {
            width: 300px;
            justify-content: space-between;
            padding-left: 30px;
        }
    }

    &--plug {
        padding: 10px 16px;
    }

    &--success {
        background: var(--dtn-panel__background--success);
    }
}

.dtn-panel + .dtn-panel {
    margin-top: 20px;
}

.general-panel--success {
    background: var(--dtn-panel__background--success);
    box-shadow: 0 2px 12px 1px rgb(39 174 96 / 60%);
}
