@keyframes dtn-loop-gradient {
    from {
        background-position: right center;
    }

    to {
        background-position: left center;
    }
}

.dtn-skeleton {
    --dtn-skeleton__background-grd-start__color: #808080;
    --dtn-skeleton__background-grd-mid__color: #232329;
    --dtn-skeleton__background-grd-end__color: #000;
    display: block;
    width: 100%;
    height: 12px;
    background: linear-gradient(
        90deg,
        var(--dtn-skeleton__background-grd-start__color) 0%,
        var(--dtn-skeleton__background-grd-mid__color) 60%,
        var(--dtn-skeleton__background-grd-end__color) 100%
    );
    background-size: 300% 100%;
    border-radius: 12px;
    min-width: 140px;
    margin-top: 5px;
    animation: dtn-loop-gradient 1s infinite;
}
