.dtn-chart-dataset-group {
    position: relative;
    width: 178px;

    .dtn-chart-label {
        position: relative;
        z-index: 1;
    }

    .dtn-panel--absolute {
        z-index: 0;
        padding-left: 24px;
    }

    &--container {
        display: flex;
        width: 540px;
        flex-wrap: wrap;

        .dtn-chart-label {
            margin-right: 2px;
            margin-bottom: 2px;
        }

        .dtn-chart-label__break {
            width: 0;
            flex-basis: 100%;
        }
    }
}

.dtn-chart-label-container {
    position: absolute;
    top: 0;
    left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 4px;
}

.dtn-chart-label {
    --dtn-chart__color--hidden: var(--dtn-clr-secondary-lt);
    background: #333;
    border-radius: 8px;
    font-size: 18px;
    padding-right: 6px;

    &__content {
        display: flex;
        width: auto;
        line-height: 35px;
        align-items: center;
        padding-left: 8px;
        justify-content: space-between;

        &__text {
            min-width: 38px;
            font-weight: 600;
        }

        &__value {
            min-width: 7ch;
            text-align: right;
            font-weight: 800;
            flex-grow: 1;
            flex-basis: 0;

            &:last-child {
                width: 9ch;
            }
        }

        &__color {
            width: 4px;
            height: 13px;
            border-radius: 4px;
            margin-right: 3px;
        }

        &__toggle-button {
            width: 24px;
            height: 24px;
            background: 0 0 no-repeat url('/assets/icons/eye-on.svg');
            cursor: pointer;
            margin-left: 3px;
        }
    }

    &--hiddenDataset {
        .dtn-chart-label__content__color {
            background: var(--dtn-chart__color--hidden) !important;
        }

        .dtn-chart-label__content__text {
            color: var(--dtn-chart__color--hidden);
        }

        .dtn-chart-label__content__value {
            display: none;
        }

        .dtn-chart-label__content__toggle-button {
            background: 0 0 no-repeat url('/assets/icons/eye-off.svg');
            margin-left: 11px;
        }
    }

    &--fitContent {
        width: fit-content;
    }
}
