.dtn-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    vertical-align: text-bottom;

    &--xl {
        width: 48px;
        height: 48px;
    }

    &-arrow-up {
        background: 50% 50% url('/assets/icons/arrow-up.svg') no-repeat;
    }

    &-more {
        background: 50% 50% url('/assets/icons/more.svg') no-repeat;
    }

    &--upsidedown {
        transform: rotateX(180deg);
    }

    &-blow {
        background: 50% 50% url('/assets/icons/ic-purge.svg') no-repeat;
    }

    &-check {
        background: 50% 50% url('/assets/icons/check.svg') no-repeat;
    }
}
